.mint-hnft {
  .title-container {
    display: flex;
    flex-direction: column;
    font-weight: 700;
    color: black;

    .title {
      display: flex;
      align-items: center;
      font-size: 36px;
    }

    .sub-title {
      font-size: 20px;
    }
  }

  .select-network {
    margin-top: 56px;
    font-size: 20px;
    margin-bottom: 118px;

    .title {
      margin-bottom: 24px;
      font-weight: 700;
    }

    .network-list {
      display: flex;
    }

    .network-item {
      height: 110px;
      width: 110px;
      border: 1px solid rgba(28, 34, 45, 0.2);
      box-shadow: 0px 80px 120px rgba(0, 0, 0, 0.05);
      color: rgba(28, 34, 45, 0.4);
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;

      &:hover {
        cursor: pointer;
      }

      &+div {
        margin-left: 40px;
      }

      .network-name {
        margin-top: 8px;
      }
    }
  }

  .loading-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    background-color: #f3f3f3;
    border-radius: 10px;

    .ant-spin-spinning {
      margin: auto;
    }
  }

  .no-nfts-container {
    background-color: #f3f3f3;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 80px 20px;
    border-radius: 10px;

    .tips {
      margin-top: 26px;
      font-size: 20px;
      color: rgba(28, 34, 45, 0.3);
    }
  }

  .buttons {
    margin-top: 40px;
    width: 100%;
    display: flex;
    justify-content: center;

    button {
      width: 40%;

      &+button {
        margin-left: 16px;
      }
    }
  }

  .nfts-container {
    display: flex;
    flex-direction: column;
    align-items: center;

    .hnftCard {
      margin-bottom: 20px;
    }
  }
}

@media only screen and (max-width: 991px) {
  .mint-hnft {
    .no-nfts-container {
      background-color: white;
    }

    .select-network {
      margin-bottom: 0;
      overflow: hidden;
      margin-top: 45px;

      .network-item {
        width: 80px;
        height: 88px;
        min-width: 80px;
        font-size: 16px;  
      }

      .network-list {
        overflow: auto;
        padding-bottom: 8px;
      }
    }

    .buttons {
      button {
        width: 100%;
        height: 62px;
        border-radius: 36px;

        &+button {
          margin-left: 0;
          margin-top: 16px;
        }
      }

      flex-direction: column;
    }
  }
}