@import '../../styles.scss';

.hnft {
  margin: 0 300px;

  .title {
    font-size: 36px;
    line-height: 42px;
    color: #10121A;
    font-weight: 600;
    margin-bottom: 40px;
  }

  .my-hnft {
    margin-bottom: 60px;

    .billboard-nft-Legendary {
      .description {
        display: none;
      }
    }

    .auth-btn-container {
      margin-top: 10px;
    }
  }

  .my-token {
    padding: 50px 0;
    border-top: 1px solid rgba(28, 34, 45, 0.1);
  }

  .token {
    padding: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #F2F2F2;
    border: 1px solid rgba(214, 214, 214, 1);
    border-radius: 30px;
    max-width: 580px;
    font-size: 28px;
    color: #10121A;

    &-image {
      width: 82px;
      height: 82px;
      border-radius: 10px;
      margin-right: 35px;
      background: white;
      padding: 14px;

      >img {
        width: 100%;
        height: 100%;
      }
    }

    &-detail {
      display: flex;
      align-items: center;
      font-weight: 600;
    }

    &-price {
      font-weight: 700;
    }

    &-type {
      color: rgba(28, 34, 45, 0.2);
    }
  }

  .issue-my-first-token {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 24px;
    margin-bottom: 64px;
    button {
      height: 52px;
      width: 80%;
    }
  }
}

@media only screen and (max-width: 991px) {
  .hnft {
    margin: 0;

    .my-hnft {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-bottom: 10px;
    }

    .mobile-billboard-nft-image {
      .svg-container {
        margin: 0;
        width: 222px;
        height: 222px;
      }
    }

    .my-token {
      border: none;
      padding-bottom: 0;
    }

    .token {
      padding: 24px;
      font-size: 18px;

      &-image {
        width: 50px;
        height: 50px;
        margin-right: 16px;
      }

      &-price {
        font-size: 22px;
      }
    }
  }
}